export default function TransitionLayer({
  showTransition,
}: {
  showTransition: boolean;
}) {
  return (
    <div
      className={`fixed inset-0 z-[999] flex ${
        showTransition && '-translate-y-full'
      } items-center justify-center bg-primary transition-all duration-500`}
    >
      <div className="rounded-lgp-6 relative text-white">
        <span className="absolute top-0 h-4 w-4 animate-ping rounded-full bg-white opacity-75" />
        <span className="font-primary text-2xl">IPB Virtual Fair 2022</span>
      </div>
    </div>
  );
}
